<template>
  <div class="login-container">
    <img class="head-image" src=""/>
    <div class="register-tag-view btn-text">
      新用户注册
    </div>
    <div class="input-view">
      <login-input cla @changeValue="phoneChange" place-holder="请输入手机号"></login-input>
    </div>
    <div class="input-view">
      <login-input @changeValue="authCodeChange" place-holder="请输入验证码"></login-input>
    </div>
    <div class="next-btn" @click="nextStep">下一步</div>
    <div class="foot-view btn-text" @click="backLogin">返回登录</div>
  </div>
</template>

<script>

  import loginInput from '../../components/common/loginInput';
  import { navigateTo, navgationBack } from '../../utils/mRouter';

  export default {
    name: 'register',
    components: { loginInput },
    methods: {
      phoneChange(value) {
        console.log(value);
      },
      authCodeChange(value) {
        console.log(value);
      },
      nextStep() {
        navigateTo('../register_password/register_password', 'register_password', '', this);
      },
      backLogin() {
        navgationBack(-1,this);
      },
    },
  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .login-container {
  .flx-y;
  }
  .head-image {
    .wh(2,2);
    .borderRadius(1);
    .margin-top(0.24);
    background-color: gold;
  }
  .register-tag-view {
    .margin-top(0.32);
    .text-color1;
    .fontSize-two;
  }
  .btn-text {
    .text-color3;
    .fontSize-five;
  }


  .next-btn {
    .wh(6.06,0.88);
    .borderRadius(0.08);
    .flx-x-center;
    .margin-top(0.72);
    .fontSize-three;
    background-color: #45D4FB;
    color: white;
  }
  .foot-view {
    position: absolute;
  .height(0.88);
  .line-height(0.88);
    bottom: 20px;
  }
  .input-view {
  .wh(7.5,1.4);
  .flx-y-end;
  }

</style>



